'use client';

import React, { createContext, useContext } from 'react';
import { useCartStore } from '@/store/cartStore'; // Assuming you have this Zustand store

// Create context
export const CartContext = createContext<{
  items: any[];
  loading: boolean;
  error: string | null;
  cartCount: number;
  cartTotal: number;
  clearCart: () => void;
} | undefined>(undefined);

// Provider component
interface CartProviderProps {
  children: React.ReactNode;
}

export const CartProvider: React.FC<CartProviderProps> = ({ children }) => {
  const { items, clearCart } = useCartStore();

  // Calculate cart count and total from Zustand store
  const cartCount = items.reduce((total, item) => total + (item.quantity || 0), 0);
  const cartTotal = items.reduce((total, item) => total + ((item.price || 0) * (item.quantity || 0)), 0);

  return (
    <CartContext.Provider
      value={{
        items,
        loading: false, // Loading state would be handled separately if needed
        error: null,    // Error state would be handled separately if needed
        cartCount,
        cartTotal,
        clearCart
      }}
    >
      {children}
    </CartContext.Provider>
  );
};

export const useCart = () => {
  const context = useContext(CartContext);
  if (context === undefined) {
    throw new Error('useCart must be used within a CartProvider');
  }
  return context;
};