'use client';

import React, { createContext, useContext, useReducer } from 'react';

interface ProductState {
  products: any[];
  loading: boolean;
  error: string | null;
  appointments: any[];
  orders: any[];
}

interface ProductAction {
  type: string;
  payload?: any;
}

// Action types
const PRODUCT_ACTIONS = {
  SET_PRODUCTS: 'SET_PRODUCTS',
  SET_LOADING: 'SET_LOADING',
  SET_ERROR: 'SET_ERROR',
  SET_APPOINTMENTS: 'SET_APPOINTMENTS',
  SET_ORDERS: 'SET_ORDERS',
  ADD_ORDER: 'ADD_ORDER'
};

// Initial state
const initialState: ProductState = {
  products: [],
  loading: false,
  error: null,
  appointments: [],
  orders: []
};

// Reducer
const productReducer = (state: ProductState, action: ProductAction): ProductState => {
  switch (action.type) {
    case PRODUCT_ACTIONS.SET_PRODUCTS:
      return {
        ...state,
        products: action.payload
      };
    case PRODUCT_ACTIONS.SET_LOADING:
      return {
        ...state,
        loading: action.payload
      };
    case PRODUCT_ACTIONS.SET_ERROR:
      return {
        ...state,
        error: action.payload
      };
    case PRODUCT_ACTIONS.SET_APPOINTMENTS:
      return {
        ...state,
        appointments: action.payload
      };
    case PRODUCT_ACTIONS.SET_ORDERS:
      return {
        ...state,
        orders: action.payload
      };
    case PRODUCT_ACTIONS.ADD_ORDER:
      return {
        ...state,
        orders: [action.payload, ...state.orders]
      };
    default:
      return state;
  }
};

// Create context
export const ProductContext = createContext<{
  products: any[];
  loading: boolean;
  error: string | null;
  appointments: any[];
  orders: any[];
  setProducts: (products: any[]) => void;
  setLoading: (loading: boolean) => void;
  setError: (error: string | null) => void;
  setAppointments: (appointments: any[]) => void;
  setOrders: (orders: any[]) => void;
} | undefined>(undefined);

// Provider component
interface ProductProviderProps {
  children: React.ReactNode;
}

export const ProductProvider: React.FC<ProductProviderProps> = ({ children }) => {
  const [state, dispatch] = useReducer(productReducer, initialState);

  // Actions
  const setProducts = (products: any[]) => {
    dispatch({ type: PRODUCT_ACTIONS.SET_PRODUCTS, payload: products });
  };

  const setLoading = (loading: boolean) => {
    dispatch({ type: PRODUCT_ACTIONS.SET_LOADING, payload: loading });
  };

  const setError = (error: string | null) => {
    dispatch({ type: PRODUCT_ACTIONS.SET_ERROR, payload: error });
  };

  const setAppointments = (appointments: any[]) => {
    dispatch({ type: PRODUCT_ACTIONS.SET_APPOINTMENTS, payload: appointments });
  };

  const setOrders = (orders: any[]) => {
    dispatch({ type: PRODUCT_ACTIONS.SET_ORDERS, payload: orders });
  };

  return (
    <ProductContext.Provider
      value={{
        products: state.products,
        loading: state.loading,
        error: state.error,
        appointments: state.appointments,
        orders: state.orders,
        setProducts,
        setLoading,
        setError,
        setAppointments,
        setOrders
      }}
    >
      {children}
    </ProductContext.Provider>
  );
};

export const useProduct = () => {
  const context = useContext(ProductContext);
  if (context === undefined) {
    throw new Error('useProduct must be used within a ProductProvider');
  }
  return context;
};